/*General Styles*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font: normal 16px sans-serif;
  color: #555;
}

ul,
nav {
  list-style: none;
}

a {
  text-decoration: none;
  opacity: 0.75;
  color: black;
}

a:hover {
  opacity: 1;
}

.container {
  margin: 0 10vw;
}

@media (max-width: 400px) {
  .container {
    margin: 0;
  }
}

.styles_playButton__1Q3EF {
  display: none;
}

.header {
  background: url(/assets/images/header_bg01.png) no-repeat left;
  background-size: cover;
  box-shadow: 0px -3px 10px 0px dimgray;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header .nav {
  display: -webkit-box;
  display: flex;
  justify-content: space-around;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  padding: 5px 0;
}

.header .nav-brand {
  margin: 0 2vw 0 0;
}

.header .nav-list {
  display: -webkit-box;
  display: flex;
  justify-content: space-around;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  width: 100%;
  margin: 0 5vw;
}

.header .nav-list-item {
  text-transform: uppercase;
}

.header .nav-toggle>i {
  color: #199251;
  font-size: 1.5rem;
  display: none;
}

@media (max-width: 950px) {
  .header .nav {
    display: -webkit-box;
    display: flex;
    justify-content: space-around;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 5px 0;
  }

  .header .nav-brand {
    margin: 0 2vw 0 0;
    width: 100%;
    display: -webkit-box;
    display: flex;
    justify-content: space-around;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    flex-direction: row;
  }

  .header .nav-list {
    display: -webkit-box;
    display: flex;
    justify-content: space-around;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
    padding: .5em 0 .5rem 0;
    margin: 0;
  }

  .header .nav-list-item {
    padding-top: .2rem;
  }

  .header .nav #active {
    display: none;
  }

  .header .nav-toggle>i {
    display: block;
    float: right;
  }
}

footer {
  background: url(/assets/images/footer_bg.jpg) no-repeat left;
  background-size: cover;
  position: relative;
  bottom: 0;
}

footer .footer {
  display: -webkit-box;
  display: flex;
  justify-content: space-around;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  padding: 10px 0;
}

footer .footer-box {
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  -webkit-box-pack: justify;
  justify-content: space-between;
  width: 100%;
  padding: 1rem 0 3rem 0;
}

footer .footer-list {
  display: -webkit-box;
  display: flex;
  justify-content: space-around;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
}

footer .footer-list-item {
  text-transform: uppercase;
  font-size: 0.8rem;
  display: -webkit-box;
  display: flex;
  justify-content: space-around;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
}

footer .footer-list-item a {
  color: white;
  margin-left: 3vw;
}

footer .footer-right {
  color: #cccccc;
  font-size: 0.7rem;
}

@media (max-width: 950px) {
  footer .footer-box {
    display: -webkit-box;
    display: flex;
    justify-content: space-around;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
    text-align: center;
  }

  footer .footer-list {
    display: -webkit-box;
    display: flex;
    justify-content: space-around;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
  }

  footer .footer-list-item {
    margin-top: .5rem;
  }

  footer .footer-right {
    margin-top: 2rem;
    display: -webkit-inline-box;
    display: inline-flex;
    color: #cccccc;
    text-align: center;
  }
}

.main {
  background: url(/assets/images/main_bg.jpg) no-repeat;
  background-size: contain;
  height: 100%;
}

.main img {
  width: 100%;
  height: auto;
}

.main .heading {
  font-size: 130%;
  color: #199251;
  padding-bottom: 0.5rem;
  border-bottom: 0.1rem solid #199251;
}

.main .green-bold {
  color: #199251;
  font-weight: 600;
}

.main .mt {
  margin-top: 1.5rem;
}

.main .green-word {
  color: #199251;
}

.main button {
  font-size: .8rem;
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  padding: 0 1.2vw 0 1.2vw;
  -webkit-box-align: center;
  align-items: center;
  color: #199251;
  background-color: white;
  outline: none;
  border: 0.6px solid #9dd4b3;
  font-weight: 700;
  word-spacing: .1rem;
  letter-spacing: .1em;
}

.main button img {
  width: .6rem;
  height: .6rem;
}

@media (max-width: 950px) {
  .main button {
    font-size: .6rem;
    justify-content: space-around;
  }
}

.main .wrapper {
  padding: 2rem 3rem;
}

.main .container-container {
  margin: 0;
  border: 0.6px solid #e6e6e6;
  width: 100%;
  margin-bottom: 2rem;
  background: white;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(13%, white), color-stop(92%, whitesmoke), color-stop(98%, #f0f0f0));
  background: linear-gradient(180deg, white 13%, whitesmoke 92%, #f0f0f0 98%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff", endColorstr="#f0f0f0", GradientType=1);
}

.main .slider {
  position: relative;
  top: -0.8rem;
  z-index: 98;
  border: 0.6px solid #e6e6e6;
}

.main .slider-content {
  position: absolute;
  height: 50%;
  width: 30%;
  z-index: 100;
  top: 20%;
  left: 68%;
  color: black;
  font-size: 1.5vw;
  font-weight: 300;
}

.main .slider-content-header {
  color: #199251;
  font-size: 2vw;
}

.main .slider button {
  width: 20%;
  height: 10%;
  z-index: 100;
  top: 80%;
  right: 10%;
  position: absolute;
  background-color: white;
  outline: none;
  cursor: pointer;
  font-weight: 700;
  word-spacing: .1rem;
  letter-spacing: .1em;
}

.main .slider button img {
  width: 1vw;
  height: 1vw;
}

@media screen and (max-width: 620px) {
  .main .slider button {
    font-size: 35%;
  }
}

.main .lifestyle .wrapper-inner {
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  padding: 1rem 0 1rem 0;
}

.main .lifestyle .wrapper-inner-left {
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  flex-direction: column;
  max-width: 550px;
  font-size: 1rem;
  -webkit-box-pack: justify;
  justify-content: space-between;
}

.main .lifestyle .wrapper-inner-left button {
  margin-top: 3vh;
  width: 10rem;
  height: 2rem;
}

.main .lifestyle .wrapper-inner-right {
  display: -webkit-box;
  display: flex;
  padding-left: 1rem;
}

.main .lifestyle .wrapper-inner-right-block {
  border-left: 1px solid #cdcdcd;
  padding-left: 0.5rem;
}

.main .lifestyle .wrapper-inner-right-block-header {
  padding-left: 1rem;
  width: 100%;
  color: #9a9a9a;
}

.main .lifestyle .wrapper-inner-right-block-main {
  display: -webkit-box;
  display: flex;
  justify-content: space-around;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  width: auto;
  -webkit-box-align: start;
  align-items: flex-start;
}

.main .lifestyle .wrapper-inner-right-block-main-inner {
  padding: 0 10px;
  max-width: 150px;
}

.main .lifestyle .wrapper-inner-right-block-main-inner:nth-child(2) {
  border-left: 1px solid #cdcdcd;
}

.main .lifestyle .wrapper-inner-right-block-main-inner-title {
  color: #199251;
}

@media screen and (max-width: 950px) {
  .main .lifestyle .wrapper-inner {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
  }

  .main .lifestyle .wrapper-inner-left {
    max-width: 100%;
    margin-bottom: 1.5rem;
  }

  .main .lifestyle .wrapper-inner-right {
    padding: 0;
  }

  .main .lifestyle .wrapper-inner-right-block:nth-child(1) {
    border-left: 0px;
  }

  .main .lifestyle .wrapper-inner-right-block-header {
    padding: 0 0 .3rem 1rem;
  }
}

@media screen and (max-width: 950px) and (max-width: 620px) {
  .main .lifestyle .wrapper-inner-right {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
  }

  .main .lifestyle .wrapper-inner-right-block {
    padding-bottom: 1rem;
  }

  .main .lifestyle .wrapper-inner-right-block:nth-child(2) {
    border-left: 0px;
  }

  .main .lifestyle .wrapper-inner-right-block-main-inner {
    padding: 0;
  }

  .main .lifestyle .wrapper-inner-right-block-main-inner:nth-child(2) {
    border: none;
  }
}

.main .customer-news-wrapper {
  margin-top: 2rem;
  display: -webkit-box;
  display: flex;
  justify-content: space-around;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  -webkit-box-align: start;
  align-items: flex-start;
  width: 100%;
  -webkit-box-pack: justify;
  justify-content: space-between;
}

.main .customer-news-wrapper .customer {
  width: 55%;
}

.main .customer-news-wrapper .customer-card {
  margin-top: .5rem;
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  -webkit-box-pack: center;
  justify-content: center;
  padding-bottom: 1rem;
}

.main .customer-news-wrapper .customer-card img {
  width: 32%;
}

@media screen and (max-width: 950px) {
  .main .customer-news-wrapper .customer-card img {
    width: 45%;
  }
}

@media screen and (max-width: 620px) {
  .main .customer-news-wrapper .customer-card img {
    width: 7rem;
  }
}

@media screen and (max-width: 620px) {
  .main .customer-news-wrapper .customer {
    width: auto;
  }
}

.main .customer-news-wrapper .news {
  width: 35%;
  padding-bottom: 1rem;
}

.main .customer-news-wrapper .news-articles {
  padding-bottom: 2rem;
}

.main .customer-news-wrapper .news-articles:nth-child(2) {
  border-bottom: 1px solid #cdcdcd;
}

.main .customer-news-wrapper .news-articles-date {
  padding-top: 3px;
  color: #4e4e4e;
  padding-bottom: 1rem;
  font-weight: 500;
  font-size: 90%;
}

@media screen and (max-width: 620px) {
  .main .customer-news-wrapper .news {
    width: auto;
  }
}

@media screen and (max-width: 620px) {
  .main .customer-news-wrapper {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
  }
}

@media screen and (max-width: 400px) {
  .main .customer-news-wrapper {
    margin: 0 .8rem;
  }
}

.media {
  background: white;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(13%, white), color-stop(92%, whitesmoke), color-stop(98%, #f0f0f0));
  background: linear-gradient(180deg, white 13%, whitesmoke 92%, #f0f0f0 98%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff", endColorstr="#f0f0f0", GradientType=1);
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
}

.media-block {
  width: 45%;
  margin: 0px;
}

@media screen and (max-width: 620px) {
  .media-block {
    width: 100%;
    padding-bottom: 2rem;
  }
}

.media-block-gallery {
  margin: 2rem 0;
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  flex-wrap: wrap;
}

.media-block-gallery .thumbnail {
  width: 15.5vw;
}

@media screen and (max-width: 950px) {
  .media-block-gallery .thumbnail {
    width: 13vw;
  }
}

@media screen and (max-width: 620px) {
  .media-block-gallery .thumbnail {
    width: 25.5vw;
  }
}

@media screen and (max-width: 620px) {
  .media {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
  }
}

.fancybox-navigation {
  all: unset;
  border: 2px solid red;
}

.fancybox-inner {
  all: unset;
}